<script>
export default {
  name: "XinZeng",
  data() {
    return {
      gridData: {
        name: '',
        yixiang: '',
        leixing: '',
        hangye: '',
        xingzi: '',
        haoma: '',
        mansum: '',
        tousum: '',
        province: '',
        city: '',
        county: '',
        address: '', zhuname: '', phone: '', dianhua: '', chudate: '', bumen: '',
        job: '', email: ''
      },
      yidg: [],
      arr: arrAll,
      cityArr: [],
      countyArr: [],
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [{
          text: '',
          onClick(picker) {
            picker.$emit('pick', new Date());
          }
        }, {
          text: '',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24);
            picker.$emit('pick', date);
          }
        }, {
          text: '',
          onClick(picker) {
            const date = new Date();
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', date);
          }
        }]
      },
      value1: '',
      value2: '',
    };
  },
  beforeRouteEnter: (to, from, next) => {
    console.log("");
    next(vm => {
      vm.getData();
    });
  },

  beforeRouteLeave: (to, from, next) => {
    console.log("");
    next();
  },
  methods:
    {
      getCity() {
        for (var i = 0; i < this.arr.length; i++) {
          var obj = this.arr[i]
          if (this.gridData.province == obj.name) {
            this.cityArr = obj.sub;
          }
        }
      },
      getCounty() {
        for (var i = 0; i < this.cityArr.length; i++) {
          var obj = this.cityArr[i]
          if (this.gridData.city == obj.name) {
            this.countyArr = obj.sub;
          }
        }
      },
      getData: function () {
        this.zhunIdd=this.$route.query.id
        this.axios.post('http://localhost:8081/toupdate',this.zhunIdd).then(res => this.gridData = res.data)
        console.log(this.gridData)
      },
      updad(){
        console.log(this.gridData)
        this.axios.post('http://localhost:8081/updateZhun',this.gridData).then(res => this.ccc = res.data)
        console.log(this.ccc)
      }
    }
}
</script>

